.contact-github {
    height: 50px;
    width: 50px;
    border-radius: 50%;
  
    animation: social-media 2000ms ease-in 2000ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }
  .contact-linkedin {
    height: 50px;
    width: 50px;
    border-radius: 50%;
 
    animation: social-media 4000ms ease-in 2000ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }
  .contact-email {
    height: 50px;
    width: 50px;
    border-radius: 50%;
 
    animation: social-media 6000ms ease-in 2000ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }
  .contact-github:hover{
    box-shadow: pink 0px 14px 28px, pink 0px 10px 10px;
    }
    .contact-email:hover{
    box-shadow:pink 0px 14px 28px, pink 0px 10px 10px ;
    }
    .contact-linkedin:hover{
    box-shadow: pink 0px 14px 28px, pink 0px 10px 10px;
    }
  
  @keyframes social-media {
      0% {
        transform: translateX(500px);
        opacity: 0;
      }
    
      100% {
        transform: translateX(0px);
        opacity: 1;
      }
    }

    .con {
      animation: logo 2000ms ease-in 500ms;
      animation-fill-mode: forwards;
      transform: translateY(-100px);
      opacity: 0;

    }
  
    @keyframes logo {
      0% {
        transform: translateY(100px);
        opacity: 0;
      }
    
      100% {
        transform: translateY(0px);
        opacity: 1;
      }
    }
  
    .summ{
      animation: summary 2000ms ease-in 500ms;
      animation-fill-mode: forwards;
      transform: translateY(-100px);
      opacity: 0;
    }
    @keyframes summary {
      0% {
        transform: translateY(-100px);
        opacity: 0;
      }
    
      100% {
        transform: translateY(0px);
        opacity: 1;
      }
    }