.title {
  animation: text-load 2000ms ease-in 2000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}

.button {
  animation: text-load 2000ms ease-in 500ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
.button:hover{
  box-shadow:pink 0px 14px 28px,pink 0px 10px 10px;
}
.name {
  animation: text-load 2000ms ease-in 1500ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
.image {
  animation: image 10000ms ease-in-out infinite 1000ms;
  animation-fill-mode: forwards;
  transform: translateY(-20px);

}
.text {
  animation: text-load 2000ms ease-in 1000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}

@keyframes text-load {
  0% {
    transform: translateY(-300px);
    opacity: 0;
  }

  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes image {
  0% {
    transform: translateY(0px);
    opacity: 1;
  }

  25% {
    transform: translateY(-30px);
    opacity: 1;
  }
  50% {
    transform: translateY(0px);
    opacity: 1;
  }

  75% {
    transform: translateY(30px);
    opacity: 1;
  }

  100% {
    transform: translateY(0px);
    opacity: 1;
  }


}

.git {
  height: 50px;
  width: 50px;
  border-radius: 50%;

  animation: social-media 2000ms ease-in 2000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
.in {
  height: 50px;
  width: 50px;
  border-radius: 50%;
 
  animation: social-media 4000ms ease-in 2000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
.twit {
  height: 50px;
  width: 50px;
  border-radius: 50%;

  animation: social-media 6000ms ease-in 2000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
.git:hover{
box-shadow: pink 0px 14px 28px, pink 0px 10px 10px;
}
.twit:hover{
box-shadow:pink 0px 14px 28px, pink 0px 10px 10px ;
}
.in:hover{
box-shadow: pink 0px 14px 28px, pink 0px 10px 10px;
}
@keyframes social-media {
    0% {
      transform: translateX(500px);
      opacity: 0;
    }
  
    100% {
      transform: translateX(0px);
      opacity: 1;
    }
  }