.div {
    animation: div 4000ms ease-in 500ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }

  @keyframes div {
    0% {
      transform: translateY(100px);
      opacity: 0;
    }
  
    100% {
      transform: translateY(0px);
      opacity: 1;
    }
  }
  #pro{
    box-shadow: rgb(223, 194, 158) 0px 50px 100px -20px, rgb(162, 14, 247) 0px 30px 60px -30px, rgba(229, 132, 221, 0.774) 0px -2px 6px 0px inset;
  }