.logo {
    animation: logo 2000ms ease-in 500ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }

  @keyframes logo {
    0% {
      transform: translateY(100px);
      opacity: 0;
    }
  
    100% {
      transform: translateY(0px);
      opacity: 1;
    }
  }

  .summary{
    animation: summary 2000ms ease-in 500ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }
  @keyframes summary {
    0% {
      transform: translateY(-100px);
      opacity: 0;
    }
  
    100% {
      transform: translateY(0px);
      opacity: 1;
    }
  }

