.skills-card-img{
    position:absolute;
    z-index: 6;
    box-shadow: rgb(12, 12, 235) 0px 50px 100px -20px, rgb(247, 14, 14) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.skills-card-img {
    animation: text-load 2000ms ease-in 2000ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }
  @keyframes text-load {
    0% {
      transform: translateY(-300px);
      opacity: 0;
    }
  
    100% {
      transform: translateY(0px);
      opacity: 1;
    }
  }

  #a {
    /* box-shadow: 16px 14px 20px #0000008c; */
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    box-shadow: rgb(138, 0, 218) 0px 50px 100px -20px, rgb(221, 235, 28) 0px 30px 60px -30px, rgba(39, 147, 255, 0.35) 0px -2px 6px 0px inset;
    transition: 0.4s;
}
#a::before{
    content: "";
    background-image: conic-gradient(
        #ff000d 20deg,
        transparent 120deg
    );
    width: 150px;
    height: 150%;
    animation: a 4s linear infinite;
}
#a:hover{
    transform: translateY(-30px)
}
@keyframes a {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(-360deg);
    }
}
